//       https://www.youtube.com/watch?v=PqTrhfjLQBI
export function feedback() {
  if (document.getElementById('form')) {
    const form = document.getElementById('form');
    form.addEventListener('submit', formSend); //    - formSend
    // 
    async function formSend(e) {
      e.preventDefault(); //   

      let error = formValidate(form); //   
      let formData = new FormData(form); //     

      if (error === 0) {
        //         php
        let response = await fetch('sendmail.php', {
          method: 'POST',
          body: formData
        });
        if (response.ok) {
          let result = await response.json();
          console.log(result.message);
          form.reset();
          e.target.querySelector('.js_popup-thx').click();
        } else {
          alert('');
        }
      } else {
        alert('  !')
      }
    }

    function formValidate(form) {
      let error = 0;
      let formReq = form.querySelectorAll('.js_req') //require -   //       
      //     
      for (let index = 0; index < formReq.length; index++) {
        const input = formReq[index];
        formRemoveError(input); //         '__error'
        //    '__email'  
        if (input.classList.contains('js_email')) {
          //      email,     '__error'
          if (emailTest(input)) {
            formAddError(input);
            error++;
          }
        } else {
          //   
          if (input.value === '') {
            formAddError(input);
            error++;
          }
        }
      }
      return error;
    }

    // /  '__error'    
    function formAddError(input) {
      input.parentElement.classList.add('js_error');
      input.classList.add('js_error');
    }
    function formRemoveError(input) {
      input.parentElement.classList.remove('js_error');
      input.classList.remove('js_error');
    }
    //Regexp   email
    function emailTest(input) {
      return !/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,8})+$/.test(input.value);
    }
  }
}
